import type { GetLoggerInterface, Logger } from '@zg-rentals/particles-js-base-logger';
import { BaseLogger } from '@zg-rentals/particles-js-base-logger';

import child_process from 'child_process';
import fs from 'fs';
import path from 'path';
import pino from 'pino';
import stream from 'stream';

export interface PinoLoggerInterface extends GetLoggerInterface {
  logPath?: string;
  prettyPath?: string;
  pinoArgs?: pino.LoggerOptions;
}
export class PinoLogger extends BaseLogger<pino.Logger> implements Logger {
  logger: pino.Logger;
  prettyLogger?: child_process.ChildProcess;
  _enableLogSync = true;
  runtime: 'node';

  constructor({
    pinoArgs = {},
    logPath = '',
    prettyPath = 'node_modules/pino-pretty/bin.js',
    __reset = () => {},
  }: PinoLoggerInterface = {}) {
    super({ __reset, runtime: 'node' });

    this.runtime = 'node';

    const cwd = process.cwd();
    const logThrough = new stream.PassThrough();

    this.logger = pino(
      {
        level: 'info',
        ...pinoArgs,
      },
      logThrough,
    );

    if (logPath) {
      logThrough.pipe(fs.createWriteStream(logPath, { flags: 'a' }));
    }

    if (prettyPath) {
      const prettyLogger = child_process.spawn(process.execPath, [path.join(process.cwd(), prettyPath), '-c'], {
        cwd,
        env: process.env,
      });

      logThrough.pipe(prettyLogger.stdin);

      prettyLogger.stdout.pipe(process.stdout);

      this.prettyLogger = prettyLogger;
    } else {
      logThrough.pipe(process.stdout);
    }
  }

  child(name: string, bindings?: pino.Bindings): pino.Logger {
    if (name.length > 255) {
      throw new Error(`child logger name length is ${name.length}, must be less than 255`);
    }

    const logger = this.logger.child({ ...bindings, name });

    this._children.set(name, logger);

    return logger;
  }

  error(...args: Array<unknown>) {
    this.logger.error(args);
  }
  warn(...args: Array<unknown>) {
    this.logger.warn(args);
  }
  info(...args: Array<unknown>) {
    this.logger.info(args);
  }
  debug(...args: Array<unknown>) {
    this.logger.debug(args);
  }
  trace(...args: Array<unknown>) {
    this.logger.trace(args);
  }

  _getChildLogger(name: string) {
    return this._children.get(name);
  }

  setChildLogLevel(name: string, level?: string) {
    const logger = this._getChildLogger(name);

    if (logger) logger.level = level || process.env.LOG_LEVEL || 'warn';
  }
}
